
.cf-title {
  padding: 40px 0 20px 20px;
  background-color: #f8f8f8;
}
.Class-sp-list {
  width: 100%;
  margin-top: 10px;
}
.Class-sp-list .Class-sp-li {
    text-align: center;
    width: 25%;
    padding-bottom: 10px;
}
.Class-sp-list .Class-sp-li .Class-sp-pic {
      width: 232px;
      height: 232px;
      margin: 0 auto;
      background: #F4F4F4 url("../assets/images/index-sp_03.png") no-repeat center;
      background-size: cover;
      background-clip: content-box;
}
.Class-sp-list .Class-sp-li p {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.Class-sp-list .Class-sp-li .sp-name {
      padding: 14px 0 12px;
}
.Class-sp-list .Class-sp-li .sp-jiage {
      color: #F6732D;
}



