
.or-pad {
  padding-left: 20px;
}
.Z_xuanze {
  width: 960px;
  height: 80px;
  margin: 0 auto;
  margin-top: 20px;
  background: #F7F6F6;
}
.Z_xuanze p {
    width: 120px;
    height: 28px;
    line-height: 80px;
    font-size: 20px;
    margin-left: 20px;
}
.Z_address {
  width: 960px;
  /*height:200px;*/
  margin: 0 auto;
  position: relative;
}
.Z_address .Contentbox {
    width: 25%;
    margin-bottom: 30px;
    display: inline-block;
    cursor: pointer;
    vertical-align: middle;
    position: relative;
}
.Z_address .Contentbox .active {
      border: 1px solid #E5AD68 !important;
}
.Z_address .Contentbox .Content {
      position: relative;
      width: 200px;
      height: 120px;
      margin: 40px auto 0;
      border: 1px solid lightgray;
      /* display:inline-block; */
}
.Z_address .Contentbox .Content p {
        width: 180px;
        height: 34px;
        color: #333333;
        font-size: 12px;
        margin-left: 10px;
        overflow: hidden;
        word-wrap: break-word;
}
.Z_address .Contentbox .Content p span {
          line-height: 34px;
          display: inline-block;
}
.Z_address .Contentbox .Content p span:nth-child(1) {
          float: left;
}
.Z_address .Contentbox .Content p span:nth-child(2) {
          float: right;
}
.Z_address .Contentbox .Content p:nth-child(1) {
        border-bottom: 1px solid lightgray;
}
.Z_address .Contentbox .Content p:nth-child(2) {
        margin-top: 10px;
        color: #888888;
        line-height: 17px;
}
.Z_address .Contentbox .Content input {
        position: absolute;
        right: 0;
        bottom: 0px;
        display: none;
        width: 36px;
        height: 21px;
        font-size: 12px;
        color: #8D8D8D;
        border: none;
        outline: none;
        cursor: pointer;
        background: #E5E5E5;
}
.Z_address .Contentbox .Content:hover input {
        display: block;
}

/*.Contentbox>.xinjiaru{
	width:200px;
	height:120px;
	margin-top:40px;
	border:1px solid lightgray;
	display:inline-block;
}*/
.Contentbox2 {
  width: 25%;
  display: inline-block;
  cursor: pointer;
}
#Content {
  width: 200px;
  height: 120px;
  margin: 40px auto 30px;
  vertical-align: middle;
  position: relative;
  border: 1px solid lightgray;
}
#jiahao {
  width: 20px;
  height: 20px;
  color: white;
  position: absolute;
  left: 88px;
  top: 35px;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  border-radius: 50%;
  background: #E5AD68;
}
#Tdizhi {
  width: 84px;
  height: 17px;
  line-height: 17px;
  font-size: 12px;
  color: #E5AD68;
  position: absolute;
  left: 58px;
  top: 60px;
}
.Z_mask {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 20;
}
.Z_mask .maskIn {
    width: 450px;
    height: 447px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: white;
    text-align: center;
}
.Z_mask .maskIn img {
      height: 20px;
      margin-left: 20px;
      margin-top: 43px;
      display: block;
}
.Z_mask .maskIn input {
      border: 1px solid #E5E5E5;
      outline: none;
}
.Z_mask .maskIn input:nth-child(2), .Z_mask .maskIn input:nth-child(3) {
      width: 187px;
      height: 50px;
      margin: 0 7px;
      margin-top: 27px;
      padding-left: 10px;
}
.Z_mask .maskIn input:nth-child(4), .Z_mask .maskIn input:nth-child(5), .Z_mask .maskIn input:nth-child(6) {
      width: 26.5%;
      height: 50px;
      margin: 0 7px;
      margin-top: 20px;
      padding-left: 10px;
}
.Z_mask .maskIn input:nth-child(7), .Z_mask .maskIn input:nth-child(8) {
      width: 392px;
      height: 50px;
      margin: 0 7px;
      margin-top: 20px;
      padding-left: 10px;
}
.Z_mask .maskIn input:nth-child(9), .Z_mask .maskIn input:nth-child(10) {
      width: 30%;
      height: 40px;
      line-height: 40px;
      font-size: 12px;
      color: white;
      text-align: center;
      border-radius: 2px;
      border: none;
      margin: 25px 15px 0 15px;
      letter-spacing: 0.75px;
}
.Z_mask .maskIn input:nth-child(9) {
      background: #CECECE;
}
.Z_mask .maskIn input:nth-child(10) {
      background: #E5AD68;
}
.Z_list {
  width: 960px;
  height: 80px;
  margin: 0 auto;
  border-bottom: 1px solid #E5E5E5;
}
.Z_list .Z_listBox {
    width: 300px;
    height: 20px;
    float: right;
    margin-top: 30px;
}
.Z_list .Z_listBox span {
      width: 28px;
      height: 20px;
      font-size: 14px;
      line-height: 20px;
      float: right;
      color: #444444;
      margin-right: 70px;
      display: inline-block;
}
.cold {
  width: 960px;
  height: 200px;
  margin: 0 auto;
  position: relative;
}
.cold .coldM {
    width: 100px;
    height: 100px;
    margin: 40px 0 40px 40px;
    display: inline-block;
    background-color: #BD8D6A;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
}
.cold .Z_explain {
    width: 140px;
    margin-left: 30px;
    position: absolute;
    top: 40px;
    display: inline-block;
}
.cold .Z_explain p:nth-child(1) {
      line-height: 22px;
      font-size: 16px;
      color: #333333;
      font-weight: bold;
      cursor: pointer;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.cold .Z_explain p:nth-child(2) {
      height: 17px;
      line-height: 17px;
      font-size: 12px;
      margin-top: 2px;
      color: #888888;
      cursor: pointer;
}
.cold .Z_explain p:nth-child(3) {
      height: 17px;
      line-height: 17px;
      font-size: 12px;
      margin-top: 13px;
      color: #888888;
      cursor: pointer;
}
.cold .biaozhu {
    width: 300px;
    height: 17px;
    float: right;
    margin-top: 72px;
}
.cold .biaozhu span {
      width: 45px;
      height: 17px;
      font-size: 12px;
      line-height: 17px;
      color: #444444;
      margin-right: 30px;
      margin-left: 13px;
      font-weight: bold;
      display: inline-block;
}
.cold .biaozhu span:nth-child(3) {
      font-size: 16px;
      color: #FF410B;
}
.yingfu {
  width: 960px;
  height: 80px;
  margin: 0 auto;
  background: #F7F6F6;
}
.yingfu .yingfuRbox {
    width: 270px;
    float: right;
}
.yingfu .yingfuRbox span:nth-child(1) {
      width: 36px;
      height: 17px;
      line-height: 80px;
      font-size: 12px;
      color: #444444;
}
.yingfu .yingfuRbox span:nth-child(2) {
      width: 56px;
      height: 28px;
      line-height: 80px;
      font-weight: bold;
      font-size: 20px;
      color: #FF410B;
}
.yingfu .yingfuRbox input {
      width: 120px;
      height: 40px;
      margin: 20px 20px 20px 30px;
      background: #E5AD68;
      color: white;
      border: 1px solid rgba(0, 0, 0, 0.14);
      border-radius: 2px;
      cursor: pointer;
}



