
@charset "UTF-8";
.header {
  width: 100%;
  padding: 12px 0;
  background-color: #444444;
  color: #fff;
  font-size: 14px;
}
.header .login {
    margin-left: 20px;
}
.header span:not(:first-child):hover {
    cursor: pointer;
}
.header span {
    color: #fff;
}

/* 登录弹窗 */
.com-logo {
  width: 100%;
  height: 120px;
}
.com-logo .w-960 {
    height: 100%;
}
.com-logo .logo-woniu {
    position: absolute;
    width: 40px;
    height: 34px;
    background: url("./assets/images/Worm.png") no-repeat center;
    left: 0;
    top: 50%;
    -webkit-transform: translatey(-50%);
            transform: translatey(-50%);
}
.com-logo .logo-wochuang {
    position: absolute;
    width: 85px;
    height: 28px;
    background: url("./assets/images/LOGO Text.png") no-repeat center;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.com-logo .logo-woniu, .com-logo .logo-wochuang {
    background-size: cover;
    z-index: 1;
}
.com-pinzhi {
  width: 100%;
  padding: 50px 0;
}
.com-pinzhi .pinzhi-list {
    padding: 0 40px;
    text-align: justify;
    font-size: 0;
}
.com-pinzhi .pinzhi-list > li {
      display: inline-block;
      text-align: justify;
      width: 225px;
      height: 64px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
}
.com-pinzhi .pinzhi-list li:nth-child(1) {
      background-image: url("./assets/images/left.png");
}
.com-pinzhi .pinzhi-list li:nth-child(2) {
      background-image: url("./assets/images/middle.png");
}
.com-pinzhi .pinzhi-list li:nth-child(3) {
      background-image: url("./assets/images/right.png");
}
.footer {
  width: 100%;
  padding: 60px 0 58px;
  font-size: 16px;
  background-color: #444444;
}
.footer > p {
    width: 100%;
    text-align: center;
    color: #fff;
}



