
@charset "UTF-8";
.car {
  width: 100%;
  height: 100%;
}

/* 幻灯片 */
.ban-pic {
  height: 400px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.ban-pic > a {
    display: block;
    height: 100%;
    width: 100%;
    text-indent: -9999px;
}
.nav {
  width: 100%;
  margin-top: 10px !important;
}
.nav > li {
    width: 25%;
    padding: 24px 0 40px;
    text-align: center;
}
.nav > li p {
      width: 100%;
      padding: 5px 0;
      font-weight: 600;
}
.nav > li .nav-bg {
      width: 100px;
      height: 100px;
      margin: 0 auto;
      background: url("../assets/images/index-class_03.jpg") no-repeat center;
      background-size: auto;
}
.huodong {
  height: 220px;
}
.huodong > a {
    display: block;
    height: 100%;
    text-indent: -9999px;
    background: url("../assets/images/index-hd_03.png") no-repeat center;
    background-size: cover;
}
.shequ {
  margin-top: 40px !important;
  height: 143px;
  background: url("../assets/images/shequ_06.png") no-repeat center;
  background-size: cover;
}
.tejia {
  width: 100%;
}
.tejia .tejia-title {
    padding: 20px 0;
}
.tejia .tejia-title .tejia-lg {
      padding-left: 10px;
      font-size: 24px;
}
.tejia .tejia-title .tejia-sm {
      margin-left: 40px;
      font-size: 14px;
      font-weight: 400;
}
.tejia .tj-list {
    width: 100%;
}
.tejia .tj-list > li {
      width: 33.3333333333%;
      min-height: 270px;
      padding: 36px 0 20px;
      text-align: center;
}
.tejia .tj-list > li .tj-bg {
        display: block;
        width: 200px;
        height: 200px;
        /* // background-image:url("../assets/images/tj-li_03.png") no-repeat center; */
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        margin: 0 auto;
}
.tejia .tj-list > li .tj-name {
        padding: 10px 0;
}
.tejia .tj-list > li .tj-jiage {
        color: #F6732D;
}
.tuijian {
  padding: 34px 20px  28px 50px;
  background-color: #FFFCF6;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 230px;
}
.tuijian .tui-left {
    margin-top: 25px;
}
.tuijian .tui-left .tl-worm, .tuijian .tui-left .tl-wtext {
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      margin: 0 auto;
}
.tuijian .tui-left .tl-worm {
      width: 50px;
      height: 50px;
      background-image: url("../assets/images/Worm.png");
}
.tuijian .tui-left .tl-wtext {
      margin-top: 5px;
      width: 85px;
      height: 28px;
      background-image: url("../assets/images/LOGO Text.png");
}
.tuijian .tui-left .tui-title {
      padding-left: 38px;
      letter-spacing: 2px;
}
.tuijian .tui-left .tui-title > h2 {
        font-size: 24px;
        padding: 0 0 14px;
}
.tuijian .tui-left .tui-title > p {
        font-size: 14px;
        margin-top: 10px;
}
.tuijian .tui-left .tui-title > p .tt-color {
          color: #FF410B;
          font-size: 24px;
          padding: 0 10px;
}
.tuijian .tui-bg {
    width: 239px;
    height: 158px;
    background: url("../assets/images/index-class_11.png") no-repeat center;
    background-size: contain;
}
.tuijian .tui-bg > a {
      display: block;
      width: 100%;
      height: 100%;
      text-indent: -9999px;
}
.tuijian .line {
    position: relative;
}
.tuijian .line::after {
    content: "";
    display: block;
    position: absolute;
    width: 24%;
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: #222;
}
.shangpin {
  overflow: hidden;
}
.shangpin .sp-title {
    padding: 20px 0;
}
.shangpin .sp-title .sp-lg {
      padding-left: 10px;
      font-size: 24px;
}
.shangpin .sp-title .sp-sm {
      margin-left: 40px;
      font-size: 14px;
      font-weight: 400;
}
.shangpin .sp-li {
    text-align: center;
    width: 25%;
    padding-bottom: 10px;
}
.shangpin .sp-li .sp-pic {
      width: 220px;
      height: 220px;
      margin: 0 auto;
      padding: 5px;
      /* background:#F4F4F4 url("../assets/images/index-sp_03.png") no-repeat center; */
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      background-clip: content-box;
}
.shangpin .sp-li p {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.shangpin .sp-li .sp-name {
      padding: 14px 0 12px;
}
.shangpin .sp-li .sp-jiage {
      color: #F6732D;
}



