
.goods-header {
  width: 100%;
  position: relative;
  margin-top: 20px;
  margin-bottom: 70px;
}
.goods-header .gh-left {
    width: 300px;
    height: 370px;
    font-size: 0;
}
.goods-header .gh-left .gh-left-pic {
      float: left;
      width: 300px;
      height: 300px;
      background-color: #e8e8e8;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
}
.goods-header .gh-left .hg-left-bottom {
      position: relative;
      float: left;
      width: 100%;
      height: 70px;
      box-sizing: border-box;
      overflow: hidden;
      padding: 10px 0;
}
.goods-header .gh-left .hg-left-bottom .left {
        display: inline-block;
        padding: 0 7px;
        background-color: #FBFBFB;
        position: absolute;
        left: 0;
        z-index: 10;
        line-height: 50px;
}
.goods-header .gh-left .hg-left-bottom .right {
        position: absolute;
        top: 10px;
        right: 0;
        display: inline-block;
        padding: 0 7px;
        background-color: #FBFBFB;
        line-height: 50px;
}
.goods-header .gh-left .hg-left-bottom span {
        font-size: 15px;
        cursor: pointer;
}
.goods-header .gh-left .hg-left-bottom .wrap {
        position: absolute;
        width: 400px;
        left: 22px;
        bottom: 10px;
}
.goods-header .gh-left .hg-left-bottom .wrap div {
          display: inline-block;
          width: 50px;
          height: 50px;
          box-sizing: border-box;
          margin: 0 7px;
          background-image: url("../assets/images/index-sp_05.png");
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center;
}
.goods-header .gh-left .hg-left-bottom .currentImg {
        border: solid 1px #ACB72F;
}
.goods-header .gh-right {
    width: 67.9999999999999%;
    padding-left: 36px;
}
.goods-header .gh-right > h2 {
      color: #555;
      font-size: 20px;
      padding: 0 0 10px 0;
}
.goods-header .gh-right > p {
      font-size: 12px;
      color: #888888;
      padding-left: 1px;
}
.goods-header .gh-right .ghr-price {
      width: 100%;
      padding: 20px;
      background-color: #F7F6F6;
      margin-top: 30px;
      color: #000;
}
.goods-header .gh-right .ghr-price .ghr-color {
        color: #FF410B;
        font-size: 20px;
        font-weight: 800;
}
.goods-header .gh-right .ghr-colors {
      width: 100%;
      padding-top: 20px;
      padding-left: 20px;
      line-height: 30px;
}
.goods-header .gh-right .ghr-colors > span {
        color: #000;
}
.goods-header .gh-right .ghr-colors .colors-list {
        margin-left: 10px;
        overflow: hidden;
}
.goods-header .gh-right .ghr-colors .colors-list > li {
          line-height: 20px;
          padding: 5px 10px;
          margin-left: 10px;
          text-align: center;
          background-color: #e8e8e8;
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          cursor: pointer;
}
.goods-header .gh-right .ghr-colors .colors-list > li.active {
            background-color: #E5AD68;
            color: #fff;
}
.goods-header .gh-right .ghr-shu {
      padding: 10px 0 0 20px;
      line-height: 30px;
      color: #000;
}
.goods-header .gh-right .ghr-shu > span {
        padding-right: 20px;
}
.goods-header .gh-right .ghr-shu .btn-group .btn-middle {
        width: 60px;
        text-align: center;
        font-size: 12px;
        border: none;
        outline: none;
        border-bottom: 1px solid #e7e7e7;
        border-top: 1px solid #e7e7e7;
}
.goods-header .gh-right .ghr-shu .buybtn {
        text-align: center;
        color: #fff;
        padding: 0 34px;
        outline: none;
        background: #E5AD68;
        border: 1px solid rgba(0, 0, 0, 0.14);
        border-radius: 2px;
        margin-left: 80px;
}
.goods-header .gh-right .ghr-shu .buybtn:active {
        border: 1px solid rgba(0, 0, 0, 0.3);
}
.goods-pics {
  width: 960px;
  margin: 40px auto 0;
}
.goods-pics .goods-pic {
    text-align: center;
    background-color: #f8f8f8;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}



