
@charset "UTF-8";
.lg-content {
  width: 300px;
  /*input 通用*/
}
.lg-content .lg-close {
    font-size: 20px;
    position: absolute;
    top: 10px;
    right: 10px;
    color: #989898;
    z-index: 202;
}
.lg-content .color {
    color: #3497FF;
}
.lg-content .lg-title {
    width: 100%;
}
.lg-content .lg-title > span {
      font-size: 12px;
}
.lg-content .lg-title > span:hover {
      cursor: pointer;
}
.lg-content .lg-title .lg-logo {
      width: 65px;
      height: 20px;
      background: url("../assets/images/LOGO Text.png") no-repeat center;
      background-size: contain;
}
.lg-content .forget {
    text-align: right;
}
.lg-content .lg-li {
    margin-top: 16px;
    height: 40px;
    font-size: 14px;
    line-height: 40px;
}
.lg-content .lg-li input {
      outline: none;
      letter-spacing: 1px;
}
.lg-content .lg-li .text, .lg-content .lg-li .btn {
      display: block;
      width: 100%;
      height: 100%;
}
.lg-content .lg-li .text, .lg-content .lg-li .test {
      text-indent: 10px;
      border: 1px solid #e5e5e5;
}
.lg-content .lg-li .btn {
      color: #fff;
      background-color: #E5AD68;
      border: 1px solid rgba(0, 0, 0, 0.14);
      border-radius: 2px;
}
.lg-content .lg-li .test {
      width: 60%;
}
.lg-content .lg-li .test-btn {
      width: 35%;
      color: #fff;
      background-color: #676564;
      border: 1px solid rgba(0, 0, 0, 0.14);
      border-radius: 2px;
      -webkit-transition: all .3s ease;
      transition: all .3s ease;
}
.lg-content .lg-li .btn:active, .lg-content .lg-li .test-btn:active {
      opacity: 0.6;
}
.lg-content .lgoin-content {
    margin-top: 10px;
    width: 100%;
}



