
.qr-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 2;
  text-align: center;
}
.qr-wrap p {
    color: #888888;
    font-size: 13px;
    padding-bottom: 20px;
}
.qr-wrap .qr {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 300px;
    height: 300px;
    text-align: center;
    background-color: #fff;
    padding: 15px;
}
.qr-wrap .qr #qrcode {
      display: inline-block;
}
.qr-wrap .qr button {
      width: 200px;
      height: 33px;
      margin-top: 30px;
      background-color: #D9534F;
      color: #fff;
      font-size: 14px;
      outline: none;
      border: solid 1px #D43F3A;
      border-radius: 4px;
      cursor: pointer;
}
.qr-wrap .qr button:hover {
      background-color: #C9302C;
      border: solid 1px #AC2925;
}
.qr-wrap .qr button:active {
      background-color: #AB2824;
}
.pay-wrap {
  width: 960px;
  background-color: #fff;
  margin: 0 auto;
}
.pay-wrap h3 {
    height: 80px;
    background-color: #F7F6F6;
    line-height: 80px;
    font-weight: 500;
    font-size: 20px;
    box-sizing: border-box;
    padding: 0 0 0 20px;
}
.pay-wrap p {
    height: 160px;
    box-sizing: border-box;
    padding: 0 0 0 20px;
    font-size: 12px;
    margin-top: 7px;
}
.pay-wrap p span {
      margin-top: 13px;
}
.pay-wrap p span:nth-child(1) {
      display: inline-block;
      color: #FF410B;
}
.pay-wrap p .detail {
      margin-left: 30px;
      color: #657180 !important;
      font-size: 18px;
}
.pay-wrap p span:nth-child(2) {
      display: block;
}
.pay-wrap ul li {
    height: 120px;
    position: relative;
    box-sizing: border-box;
    padding: 0 38px 0 0;
    background-color: #FAFAFA;
}
.pay-wrap ul li .pay-check {
      width: 22px;
      height: 22px;
      border: 1px solid #D3CFC8;
      border-radius: 50%;
      display: inline-block;
      position: absolute;
      top: 42%;
      left: 20px;
}
.pay-wrap ul li .active {
      width: 22px;
      height: 22px;
      background-image: url(../assets/images/checked.png);
      border: 0;
}
.pay-wrap ul li .pay-content {
      display: inline-block;
      position: absolute;
      left: 63px;
      top: 28%;
}
.pay-wrap ul li .pay-content h4 {
        margin-bottom: 14px;
}
.pay-wrap ul li .img {
      height: 46px;
      background-repeat: no-repeat;
      background-size: cover;
      float: right;
      margin-top: 40px;
}
.pay-wrap ul li .alipay {
      width: 130px;
      margin-right: 9px;
      background-image: url(../assets/images/alipay.png);
}
.pay-wrap ul li .weixin {
      width: 140px;
      background-image: url(../assets/images/weixin.png);
}
.pay-wrap ul .pay-affirm {
    height: 80px;
    background-color: #FAFAFA;
    margin-top: 60px;
    box-sizing: border-box;
    padding: 20px 20px 20px 0;
}
.pay-wrap ul .pay-affirm input {
      text-decoration: none;
      width: 124px;
      height: 40px;
      border: 0;
      border-radius: 2px;
      background-color: #E5AD68;
      float: right;
      color: #fff;
}
#checked {
  width: 23px;
  height: 23px;
  background-image: url(../assets/images/checked.png);
  border: 0;
}
.pay-ma {
  width: 400px;
  height: 400px;
  box-sizing: border-box;
  padding: 50px 50px;
  background-repeat: no-repeat;
  background-size: cover;
}



