
[v-cloak] {
  display: none;
}
.buy-container {
  width: 100%;
  background-color: #fff;
}
.buy-container .buy-choose {
    width: 100%;
    height: 80px;
    line-height: 80px;
    background-color: #F7F6F6;
}
.buy-container .buy-choose span {
      cursor: pointer;
      font-size: 20px;
      margin: 0 44px;
      display: inline-block;
}
.buy-container .buy-choose .active {
      color: #E5AD68 !important;
}
.buy-container .buy-detail {
    width: 100%;
    margin-top: 16px;
    background-color: #F7F6F6;
}
.buy-container .buy-detail .buy-items {
      width: 90%;
      margin: 0 auto;
      height: 40px;
      line-height: 40px;
      background-color: #F7F6F6;
      text-align: right;
}
.buy-container .buy-detail .buy-items span {
        display: inline-block;
        /* margin-right: 80px; */
        width: 13%;
        text-align: center;
        /* text-align: right; */
}
.buy-container .buy-detail #J_buy_items span {
      margin-right: 90px;
}
.buy-container .buy-detail ul {
      background-color: #fff;
}
.buy-container .buy-detail ul li {
        width: 100%;
        border-bottom: 1px solid #F2F2F2;
        list-style: none;
}
.buy-container .buy-detail ul li .buy-list {
          margin: auto;
          width: 90%;
          box-sizing: border-box;
          padding: 12px 0;
          margin-bottom: 18px;
}
.buy-container .buy-detail ul li .buy-list p {
            font-size: 10px;
            color: #888888;
}
.buy-container .buy-detail ul li .buy-list p span {
              margin-right: 15px;
}
.buy-container .buy-detail ul li .buy-list .buy-goods-detail {
            position: relative;
            margin-top: 22px;
}
.buy-container .buy-detail ul li .buy-list .buy-goods-detail .buy-img {
              width: 70px;
              height: 70px;
              vertical-align: middle;
              display: inline-block;
              background-image: url(../assets/images/goods.jpg);
              background-repeat: no-repeat;
              background-size: cover;
              background-position: center;
}
.buy-container .buy-detail ul li .buy-list .buy-goods-detail .buy-title {
              /* position: absolute; */
              left: 92px;
              display: inline-block;
              vertical-align: middle;
}
.buy-container .buy-detail ul li .buy-list .buy-goods-detail .buy-title h3 {
                display: inline-block;
                height: 25px;
}
.buy-container .buy-detail ul li .buy-list .buy-item {
            position: absolute;
            top: 50%;
            right: 0;
            -webkit-transform: translate(0, -50%);
                    transform: translate(0, -50%);
            width: 40%;
            font-size: 10px;
            display: inline-block;
            vertical-align: middle;
}
.buy-container .buy-detail ul li .buy-list .buy-item span, .buy-container .buy-detail ul li .buy-list .buy-item div {
              display: inline-block;
              vertical-align: middle;
              width: 33%;
              text-align: center;
}
.buy-container .buy-detail ul li .buy-list .buy-item .buy-operation {
              width: 30%;
              display: inline-block;
              text-align: center;
}
.buy-container .buy-detail ul li .buy-list .buy-item #J_now {
              text-decoration: none;
              display: inline-block;
              width: 90%;
              text-align: center;
              line-height: 30px;
              border-radius: 2px;
              border: 1px solid #C59559;
              background-color: #E5AD68;
              color: #fff;
}
.buy-container .buy-detail ul li .buy-list .buy-item #logistics {
              text-decoration: none;
              cursor: pointer;
}
.buy-container .buy-detail ul li .buy-list #J_buy_item span:nth-child(1) {
            margin-right: 84px;
}
.buy-container .buy-detail ul li .buy-list #J_buy_item span:nth-child(2) {
            margin-right: 24px;
}
.buy-container .buy-detail ul li .buy-list #cancel {
            display: block;
            text-decoration: none;
            font-size: 10px;
            margin-top: 10px;
            cursor: pointer;
}
.buy-delete {
  width: 200px;
  height: 120px;
  border: 1px solid #000;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 2;
  display: none;
}
#aLi {
  display: block;
}



